.page1 {
  position: relative;
  z-index: 10;
  min-height: 100vh;
  height: 100%;
  display: flex;
  align-items: center;
  .btn {
    display: none;
  }
  @media(max-width:1023px) {
    min-height: 100%;
    padding-top: 160px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .btn {
      display: block;
      margin-top: 40px;
      width: 100%;
      text-align: center;
    }
  }
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
  animation-fill-mode: forwards;
  animation: canvasMove 40s;
  transform: scale(1.1) translate(0);
}

